    .p-3(style="background-color: #E3EEFC").mb-4
      .row.justify-content-center
        .col-lg-1.mb-lg-0.mb-3.col-4: img(src='@/assets/curso/temas/tema1/30.svg', alt='')
        .col-lg-11
          p La Poltica de Atencin Integral en Salud es entonces el conjunto de enfoques, estrategias, procesos, instrumentos, acciones y recursos #[b que centra el sistema de salud en el ciudadano], permite atender a la naturaleza y contenido del #[b derecho fundamental a la salud] y #[b orienta los objetivos del sistema de salud para generar mejores condiciones de salud de la poblacin, ordenando la intervencin de los integrantes del sistema de salud y de otros actores] responsables de garantizar la promocin de la salud, la prevencin de la enfermedad, el diagnstico, el tratamiento, la rehabilitacin, la paliacin y la muerte digna, en condiciones de #[b accesibilidad, aceptabilidad, oportunidad, continuidad, integralidad y capacidad resolutiva.] (Resolucin 2626 de 2019)
    p.mb-5(data-aos='fade-right') Esta poltica tambin considera, como marco de referencia, los determinantes sociales de la salud y define cuatro estrategias centrales:
    TabsC.color-acento-botones.mb-5
      .py-4.py-md-5(titulo="Atencin Primaria en Salud")
        .row
          .col-md-4.mb-4.mb-md-0: img(src='@/assets/curso/temas/tema1/31.png', alt='')
          .col-md-8
            p Esta estrategia es reconocida desde de la Declaracin de Alma-Ata, de acuerdo con la Conferencia Internacional sobre Atencin Primaria de Salud del ao 1978, y plantea la #[b gestin intersectorial articulada a una atencin en salud que se acerque a los sujetos y a sus entornos, partiendo de una integracin de las atenciones individuales y colectivos, as como de los diferentes niveles de complejidad de los servicios de salud y en reconocimiento de la voz de las comunidades.]
              br
              br
              |Esta estrategia parte de reconocer mtodos, tecnologas y prcticas validadas en la evidencia disponible y socialmente aceptadas, que mejoren la gestin en los servicios de salud, aumentando su capacidad resolutiva, favoreciendo la territorializacin y adscripcin poblacional, as como el desarrollo de escenarios de atencin multidisciplinarios e integrales en salud.
      .py-4.py-md-5(titulo="Gestin Integral del Riesgo en Salud")
        .row
          .col-md-6.mb-4.mb-md-0
            p.mb-0 Es una #[b estrategia transversal que convoca a todos los actores y otros sectores] a concurrir en la accin anticipatoria que permite modular o evitar la aparicin de enfermedades o alteraciones. Esto incluye la #[b identificacin temprana] cuando se presenta y su #[b abordaje integral] para disminuir los impactos en la salud y el desarrollo a travs de acciones que van desde la prevencin hasta la paliacin. Implica el #[b seguimiento y monitoreo de los riesgos para la salud, as como de los resultados en los sujetos hacia donde est orientada la accin.]
              br
              br
              |Adems, propicia mejorar la experiencia de los sujetos en su interaccin con los servicios de salud y fortalecer la sostenibilidad del sistema. Tiene un #[b componente colectivo] que se desarrolla desde las intervenciones colectivas y est a cargo de la entidad territorial y un #[b componente individual] que est en cabeza de las Entidades Promotoras de Salud- EPS o aseguradores, que se relaciona muy estrechamente con la gestin clnica que desarrolla el prestador as como con los procesos de autocuidado por parte del individuo, extendido a su familia y comunidad.
              br
              br
              |La siguiente figura representa el proceso para la Gestin Integral del Riesgo en Salud:
          .col-md-6
            .imagen-modal 
              a.boton.color-acento-botones.indicador__container(@click="modal1 = true")
                i.fas.fa-search
                .indicador--click(v-if="mostrarIndicador")

              .titulo-sexto.color-acento-contenido(data-aos='fade-right')
                h5 Figura 5
                span Ruta de atencin
              img(src='@/assets/curso/temas/tema1/33.svg', alt='')
              figcation Nota. Tomado de MinSalud (2016, p.61).

      .py-4.py-md-5(titulo="Enfoque diferencial de territorios y poblaciones")
        .row
          .col-md-4.mb-4.mb-md-0: img(src='@/assets/curso/temas/tema1/32.png', alt='')
          .col-md-8
            p La poltica identifica la integralidad desde el reconocimiento de los mbitos territoriales, donde las personas, familias y comunidades habitan y desarrollan su trayectoria vital; desde all reconoce as mismo, las particularidades y condiciones que le asisten a las personas segn su momento de curso de vida, haciendo un llamado al reconocimiento del gnero como determinante y en articulacin con los dems diferenciales que le son propios a cada uno (pasando por lo tnico, la diversidad sexual, la condicin de vctima del conflicto armado o con condicin de discapacidad entre otras).
              br
              br
              |Las personas estn determinadas por varias de estas condiciones, lo cual es abordado desde el enfoque interseccional que, a su vez, permite entender los procesos de salud  enfermedad. Las necesidades asociadas y las respuestas que cada uno puede dar frente a las situaciones son diferentes si vive en zona rural o urbana, es mujer u hombre, indgena o sin pertenencia tnica, joven a adulto mayor, si present o no una discapacidad, adems del contexto socioeconmico y relacional (redes de apoyo), que se tengan con sus familias.

      .py-4.py-md-5(titulo="Enfoque de cuidado de la salud")
        .row
          .col-md-6.mb-4.mb-md-0
            p Es entendido como aquel que presenta capacidad, toma de decisiones y acciones, en las cuales el individuo, comunidad y el Estado, acogen normas para proteger la salud propia, familiar, comunidad o del territorio. Se soporta en el autocuidado e involucra una condicin dinmica integral en diferentes momentos de su vida, igual que su capacidad para acumular autonoma, desarrollando lo que la persona quiere emprender, tal como se representa en la siguiente figura.
              br
              br
              |Se involucra tambin en este proceso el deber de la sociedad y de los sistemas de salud, donde se realizan acciones que permiten ganancias en salud. Se relaciona con el curso de vida y parte de reconocer diferentes esferas del cuidado desde el cuidar de s, de los otros y del entorno. Genera oportunidades para el desarrollo e influye en la proteccin y el bienestar, promoviendo la convivencia, el cuidado hacia lo pblico, la solidaridad, la cultura de la seguridad social y el fortalecimiento de redes de apoyo familiar y social
          .col-md-6
            .imagen-modal 
                a.boton.color-acento-botones.indicador__container(@click="modal2 = true")
                  i.fas.fa-search
                  .indicador--click(v-if="mostrarIndicador")

                .titulo-sexto.color-acento-contenido(data-aos='fade-right')
                  h5 Figura 6
                  span Estrategia del cuidado
                img(src='@/assets/curso/temas/tema1/34.svg', alt='')
                figcaption Nota. Tomado de MinSalud (2016, p.38).

    ModalA(:abrir-modal.sync="modal1")
      img(src='@/assets/curso/temas/tema1/33m.svg', alt='')
    ModalA(:abrir-modal.sync="modal2")
      img(src='@/assets/curso/temas/tema1/34m.svg', alt='')
    hr
    .p-3(style="background-color: #E3EEFC").mb-4
      .row.justify-content-center
        .col-lg-1.mb-lg-0.mb-3.col-4: img(src='@/assets/curso/temas/tema1/35.svg', alt='')
        .col-lg-11
          p En el enfoque diferencial de territorios y poblaciones, la poltica tambin resea las situaciones que pueden generar mayor vulnerabilidad para el ejercicio del derecho a la salud y que requieren precisar mecanismos y esfuerzos que logren disminuir las brechas de equidad evidenciadas; reconoce por dems, que los sujetos tienen una capacidad de agencia y la respuesta en salud debe partir de ese reconocimiento del sujeto como ser integral; y finalmente, a partir de esta comprensin, denota como esencial avanzar en la adecuacin de los servicios y de los procesos de gestin para responder de manera pertinente a las necesidades y potencialidades de las poblaciones en contexto de sus territorios.
    p.mb-5(data-aos='fade-right') Ahora bien, la poltica de accin integral tambin puede estudiarse desde su enfoque, tal como se aprecia a continuacin.
    .tarjeta.bg-slyder.p-lg-5.p-3.mb-5
      SlyderA(tipo='b')
        .row
          .col-lg-7.mb-4.mb-md-0
            h5 Enfoque de Derechos
            p Se encuentra fundamentado en la garanta de la dignidad humana, la libertad, igualdad y que no se genere discriminacin. El enfoque de derechos se encuentra en el marco establecido de la Ley estatutaria, que reconoce como sujeto de derecho y garantes en un mismo marco de exigibilidad, lo que se orienta en la promocin, proteccin y que se hagan efectivos los derechos humanos; lo que incide, en asumir que todos los seres humanos son titulares de derechos y ello implica que puede reclamarlos.
            br
            br
            |Las intervenciones presentes para la atencin en servicios del sector salud, son parte de los derechos fundamentales que se encuentran asociados a los dems dentro de la promocin y proteccin para toda persona, de modo que se deben para el desarrollo humano en el pas.
          .col-lg-5
            figure
              img(src='@/assets/curso/temas/tema1/36.png', alt='')
        .row
          .col-lg-7.mb-4.mb-md-0
            h5 Enfoque diferencial
            p Para el enfoque diferencial, aunque inmerso en el enfoque de los derechos humanos, reconoce que el curso de vida es diferente para cada persona, segn sus condiciones particulares y las situaciones y contextos en los que interacta. Involucra, visualiza y valora la diversidad, promoviendo que se elimine cualquier discurso o prctica discriminatoria basada en las diferencias. Se fundamenta en los principios de igualdad, diversidad, interculturalidad, integralidad, participacin e inclusin.
          .col-lg-5
            figure
              img(src='@/assets/curso/temas/tema1/37.png', alt='')
        .row
          .col-lg-7.mb-4.mb-md-0
            h5 Enfoque de desarrollo humano
            p Se define como el desarrollo de los derechos de una manera libre y responsable, que busca superar de forma especfica, las necesidades bsicas en las cuales se debera dar prioridad al sistema econmico (empleo o distribucin de la pobreza). Este enfoque, adems de entenderse como un equilibrio entre lo econmico y social, tambin hace parte de la produccin y calidad de vida de la poblacin; esta debe garantizar la igualdad de oportunidades con equidad en los cuales se vean integrados la educacin, y sectores invertidas en el capital humano pero que tenga en cuenta los beneficios en la sociedad y que pueda ser sostenible.
          .col-lg-5
            figure
              img(src='@/assets/curso/temas/tema1/38.png', alt='')
        .row
          .col-lg-7.mb-4.mb-md-0
            h5 Enfoque de salud familiar y comunitaria

            p Es un enfoque que se encuentra integrado con los modelos de atencin primaria en salud, y que responden a las necesidades del sector salud con la priorizacin en actividades de promocin de la salud y prevencin de la enfermedad. Estas actividades permiten que no solo las instituciones o centros de salud brinden una atencin oportuna, sino que comunidades y familias hagan parte en las capacitaciones realizadas por profesionales de talento humano siendo prctico a la hora de socializarse.
              br
              br
              |Para el desarrollo de este enfoque, la toma de decisiones en la atencin primaria en salud es fundamental, lo que hace que cada persona sea autnoma y se cuide de la mejor forma posible, siendo un bien social y teniendo acciones que le dan lugar en el acceso a los servicios que sean necesarios.
          .col-lg-5
            figure
              img(src='@/assets/curso/temas/tema1/39.png', alt='')
        .row
          .col-lg-7.mb-4.mb-md-0
            h5 Enfoque de gnero

            p Considera las diferentes oportunidades que pueden tener mujeres y hombres, sus interrelaciones y el contexto sociocultural en el cual se presentan. Para este enfoque se tienen en cuenta los roles que se pueden presentar en la sociedad y que son asignados por esta para generar igualdad y sin discriminacin, garantizar los derechos y los enfoques que se integran como el diferencial, salud familiar y desarrollo humano.
          .col-lg-5
            figure
              img(src='@/assets/curso/temas/tema1/40.png', alt='')
        .row.justify-content-center
          .col-lg-8.mb-4.mb-md-0
            h5 Enfoque de curso de vida
            p El ciclo vital y las etapas de la vida son los aspectos que conforman el enfoque de curso de vida y que es caracterizado por vulnerabilidades y oportunidades de intervencin durante el transcurso de la vida, experiencias encontradas a lo largo de la vida y planes de atencin complementaria, en los cuales tiene acceso toda persona para la atencin en salud. Tambin es reconocido el desarrollo y resultados en salud que dependen de la interaccin de los distintos factores de cada persona.
              br
              br
              |Los contextos que participan en este enfoque son aquellos que se encuentran en constante construccin en sus entornos familiares, socioculturales, econmicos y ambientales. Las rutas integrales de atencin en salud se mantienen en seguimiento constante de las etapas vividas del ser humano, las cuales, de manera determinada, brindan oportunidades ptimas para la implementacin de resultados de salud, que permitan continuar con el desarrollo de conductas, habilidades o capacidades que se van adhiriendo con cada trayectoria en el transcurso de la vida.
          .col-lg-4.col-8
            figure
              img(src='@/assets/curso/temas/tema1/41.png', alt='')
    .row.justify-content-center.mb-5
      .col-lg-8.mb-lg-0.mb-3(data-aos="fade-right")
        p Es as como la Poltica de Atencin Integral en Salud es la encargada de orientar el sistema de salud para generar las mejores condiciones en esta rea a la poblacin, mediante la regulacin de las condiciones de intervencin de la comunidad hacia el acceso de los diferentes servicios de salud que deben ser prestados de manera oportuna, eficaz y con calidad para prevencin y promocin del sistema de salud. Todo esto se encuentra integrado en los territorios en los cuales se desarrolla y que requiere plantear objetivos y estrategias alrededor de la poblacin como objetivo mismo.
          br
          br
          |Los contenidos encontrados en esta poltica son: la construccin de un marco estratgico como resumen de leyes anteriores, las cuales tenan como planteamiento y elemento fundamental la Atencin Primaria y el otro, un elemento operativo llamado Modelo Integral de Atencin en Salud (MIAS), que es el que orienta la forma como se debe operar ese sistema de salud y los agentes que lo componen.
      .col-lg-4.col-8(data-aos="fade-left"): img(src='@/assets/curso/temas/tema1/42.png', alt='')
    .row.justify-content-center.mb-5
      .col-lg-2.col-4.mb-lg-0.mb-3(data-aos="fade-right"): img(src='@/assets/curso/temas/tema1/43.svg', alt='')
      .col-lg-10(data-aos="fade-left")
        p Para esta poltica, se debe trabajar de manera integrada con: la entidad territorial, las IPS y las EPS. Estos tres elementos, no necesariamente tienen mecanismos de cualidad territorial del sistema, ya que llevan aos sin un sistema de aseguramiento y sin enfoque territorial en el rgimen contributivo, deberan tener aseguramiento y (al ente territorial), capacidad de planear sobre la necesidad de la poblacin, porque el objetivo es el individuo. El concepto anterior se puede ver representado en la siguiente grfica.
    .row.justify-content-center.mb-5
      .col-lg-10(data-aos="zoom-in")
        .tarjeta.p-5(style="background-color:#FFE0DD ")
          .titulo-sexto.color-acento-contenido(data-aos='fade-right')
            h5 Figura 7.
            span Grfica Marco Conceptual PAIS (Poltica de Atencin Integral en Salud)
          .row.justify-content-center.mb-5
            .col-lg-8
              img(src='@/assets/curso/temas/tema1/44.svg', alt='La imagen presenta el marco conceptual de la Poltica de Atencin Integral en Salud, donde se presentan la entidad territorial, las EPS y las IPS.')
              figcaption Nota. Tomado de MinSalud (2016, p.31)
    p.mb-3(data-aos='fade-right') La familia, por su parte, es el ncleo del sistema con base en la Atencin Primaria, que se definen con enfoque en Salud Familiar, con visin de abordar los diferentes riesgos y el cuidado, que permiten a su vez mantener las condiciones adecuadas en la poblacin. Dentro de este modelo de Poltica de Atencin, el elemento que enlaza a los otros tres elementos (Entidad territorial, IPS-RED y EPS) es la Gestin Integral en Salud, que aborda de manera colectiva, todos los riesgos encontrados en ambas partes, como lo indica el Plan Decenal de Salud Pblica.
      br
      br
      |La Poltica de Atencin Integral en Salud incluye como beneficios:
    ul.lista-ul--color
      li
        i.fas.fa-check
        | #[b Acceso integral en el proceso para la atencin. ]
      li
        i.fas.fa-check
        | #[b Estrategias con mayores soluciones en instituciones de primer nivel de atencin con la IPS asignada y con un equipo preparado en atencin primaria en salud. ]
      li
        i.fas.fa-check
        | #[b Adecuados procesos para la autorizacin integral en la atencin dentro de un grupo de riesgo o enfermedad. ]
      li
        i.fas.fa-check
        | #[b Acciones colectivas e individuales integradas con riesgo para la bsqueda y seguimiento activo por parte del asegurador. ]
    .p-3(style="background-color: #E3EEFC").mb-4
      .row.justify-content-center
        .col-lg-1.mb-lg-0.mb-3.col-4: img(src='@/assets/curso/temas/tema1/45.svg', alt='')
        .col-lg-11
          p La Poltica de Atencin Integral en Salud es un modelo basado en las 16 Rutas de Atencin ms una ruta de Promocin y Mantenimiento, que busca ganancia en Salud a partir de estas, que integran desde la promocin hasta la paliacin. Se reconfiguran la totalidad de los dems elementos del sistema con el objetivo de ganar confianza ciudadana y equidad social en el acceso, que es lo que plantea la ley estatutaria.
    Separador 
    #t_1_3.titulo-segundo.color-acento-contenido(data-aos='fade-right')
      h2 1.3 Lineamientos tcnicos y operativos de la Ruta Integral de Atencin para la promocin y Mantenimiento de la Salud  Manual Tcnico de la EDA-3
    .row.justify-content-center.mb--lg5.mb-3
      .col-lg-8.mb-lg-0.mb-3(data-aos="fade-right"): p Teniendo en cuenta los lineamientos tcnicos y operativos encontrados en la Resolucin 3280 de 2018, en la cual se establece la ruta de atencin adecuada para promocin y el mantenimiento de la salud, de acuerdo a la primera infancia y los rangos de edad establecidos en el manual tcnico, que permite realizar el seguimiento de nias y nios, estos permiten conocer los diferentes enfoques del desarrollo y crecimiento en los cuales se identifican las alteraciones que pueden prevenirse antes del completo y adecuado crecimiento de nias y nios, quienes tienen una oportunidad para entrar en tratamiento con el trabajo realizado por personal de talento humano en salud, quienes a su vez estn entrenados por medio de instrumentos y conocimientos claros hacia el enfoque no solo clnico sino tambin del ambiente encontrado en casa, reconociendo cules sern los cuidados que se deben establecer una vez se contine el seguimiento durante su primera infancia.
      .col-lg-4.col-8(data-aos="fade-left").d-lg-block.d-none: img(src='@/assets/curso/temas/tema1/46.png', alt='')
    .bloque-texto-g.color-acento-contenido.p-3.p-sm-4.p-md-5.mb-5
      .bloque-texto-g__img(
        :style="{'background-image':`url(${require('@/assets/curso/temas/tema1/47.jpg')})`}"
      )
      .bloque-texto-g__texto.p-4
        p.mb-0 Las intervenciones individuales se definen como el conjunto de operaciones o tcnicas en salud que se dirigen a las personas en sus diferentes momentos de vida (primera infancia, infancia, adolescencia, juventud, adultez y vejez), y a la familia; la finalidad es: valoracin integral, deteccin temprana, proteccin especfica y la educacin para la salud de forma individual, grupal o familiar.
    .row.justify-content-center.mb-5
      .col-lg-2.col-4.mb-lg-0.mb-3(data-aos="fade-right"): img(src='@/assets/curso/temas/tema1/48.svg', alt='')
      .col-lg-10(data-aos="fade-left")
        p Las intervenciones individuales tienen el propsito de fortalecer las capacidades para el cuidado de la salud de las personas, minimizar el riesgo de enfermar o derivar oportunamente a rutas de grupo de riesgo o a la atencin en salud requerido para el manejo de su condicin de salud. A continuacin, se describen las intervenciones en primera infancia:
    .titulo-sexto.color-acento-contenido(data-aos='fade-right')
      h5 Figura 8.
      span Tabla de Atencin Individual RPMS
    figure.mb-5(data-aos="zoom-in")
      img(src='@/assets/curso/temas/tema1/49.png', alt='')
      figcaption Nota. Tomado de Ministerio de Salud  Grupo Curso de Vida (2022).
    p.mb-5(data-aos='fade-right') Esta atencin se brinda en el marco de la valoracin integral en salud para la primera infancia, como un procedimiento que articula una serie de acciones esenciales para aportar, en los resultados de salud, desde una perspectiva de integralidad e incorporando elementos y principios del enfoque de curso de vida, propios de la primera infancia; como el momento vital en los cuales de acuerdo al contexto se pueden reconocer cules son esas caractersticas que determinan, sea biolgica, fsica, sociocultural, que influyen en el desarrollo cerebral, interactuando de manera constante. Todo esto teniendo en cuenta en dnde estn ubicados las nias y los nios en la primera infancia. A partir de all, a continuacin se exponen los objetivos de la Resolucin 3280.
    .tarjeta.p-2.color-acento-contenido.mb-3.rounded-2.mb-1
          h4.text-center.mb-0 Objetivos de la Resolucin la 3280 del 2018
    .row.justify-content-center.mb-4
      .col-lg-3.mb-lg-0.mb-3
        .tarjeta.card-hover.p-4.h-100
          img(src='@/assets/curso/temas/tema1/50.svg', alt='' style="width: 77px").mb-3
          p.mb-0 #[b 1.] Valorar y hacer seguimiento de la salud y el desarrollo integral (fsico, cognitivo, social) de los nios y las nias.
      .col-lg-3.mb-lg-0.mb-3
        .tarjeta.card-hover.p-4.h-100
          img(src='@/assets/curso/temas/tema1/51.svg', alt='' style="width: 77px").mb-3
          p.mb-0 #[b 2.] Identificar tempranamente la exposicin o presencia de factores de riesgo, con el fin de prevenirlos o derivarlos para su manejo oportuno.
      .col-lg-3.mb-lg-0.mb-3
        .tarjeta.card-hover.p-4.h-100
          img(src='@/assets/curso/temas/tema1/52.svg', alt='' style="width: 77px").mb-3
          p.mb-0 #[b 3.] Detectar de forma temprana alteraciones que afecten negativamente la salud y el proceso de crecimiento y desarrollo, con el fin de referirlas para su manejo oportuno.
      .col-lg-3.mb-lg-0.mb-3
        .tarjeta.card-hover.p-4.h-100
          img(src='@/assets/curso/temas/tema1/53.svg', alt='' style="width: 77px").mb-3
          p.mb-0 #[b 4.] Potenciar capacidades, habilidades y prcticas para promover el cuidado de la salud y fortalecer el desarrollo de los nios (as), mediante la informacin en salud con padres o cuidadores y nios (as).
    p.mb-5(data-aos='fade-right') Sumado a esto, en la siguiente figura, se podrn identificar los valores de un contexto determinado en atencin a la salud de la primera infancia y cules son esos aspectos que se deben tener en cuenta con las intervenciones. Cada uno de estos permite que sea valorado no solo en un proceso biolgico sino adems psicolgico o social, sobre qu respuestas pueden estar reflejadas en cada individuo.
