    Separador
    #t_1_4.titulo-segundo.color-acento-contenido(data-aos='fade-right')
      h2 1.4 Valoracin integral en salud en primera infancia
    .row.justify-content-center.mb-5
      .col-lg-8.mb-lg-0.mb-3(data-aos="fade-right")
        p  La valoracin y seguimiento realizado, no solo inicia desde la primera infancia o la vida extrauterina; este proceso viene desde la concepcin, en donde la madre gestante estar con controles prenatales, los cuales permiten la prevencin o tratamiento oportuno de diagnsticos que pueden presentarse durante su etapa gestacional. Una vez comprendido esto, el seguimiento y control para la valoracin integral en la primera infancia sigue su trayecto desde el nacimiento de la nia o el nio, el cual requiere de atencin oportuna en su crecimiento y desarrollo, los cuales estn a cargo de intervenciones, y el trabajo integral entre profesionales de salud entrenados y la orientacin y capacitacin a padres y/o cuidadores del menor.
          br
          br
          |La valoracin integral tiene como objetivo conocer y comprender cules son esas directrices actuales de obligatorio cumplimiento para la promocin y el mantenimiento de la salud, con enfoque claro hacia la primera infancia. Para que este objetivo sea entendido de manera correcta, se establece en la Resolucin 3280 de 2018 expedida por el Ministerio de Salud. All se establecen cules son las rutas integrales que deben tener en cuenta para la atencin individual y las necesidades que requieren nias o nios en su valoracin y seguimientos, de acuerdo con la edad, la identificacin de factores de riesgo, la deteccin temprana de enfermedades, y no menos importante, que se potencialice capacidades, habilidades y buenas prcticas, las cuales estarn a cargo de medicina general, pediatras, mdicos familiares, enfermera y dems profesionales entrenados para la atencin en primera infancia.
      .col-lg-4.col-8(data-aos="fade-left"): img(src='@/assets/curso/temas/tema1/54.png', alt='')
    .p-3(style="background-color: #E3EEFC").mb-4
      .row.justify-content-center
        .col-lg-1.mb-lg-0.mb-3.col-4: img(src='@/assets/curso/temas/tema1/55.svg', alt='')
        .col-lg-11
          p Ahora bien, #[b Quines son las nias y nios con mayor riesgo?] Aquellos que presentan bajo peso al nacer o son prematuros, quienes presentan anomala congnita sea mayor o mltiple, que hayan requerido de hospitalizacin en su etapa neonatal mayor a una semana, condiciones poco favorables o crnicas y que presenten discapacidad. Tambin se pueden presentar algunos casos donde se sospeche problemas de desarrollo infantil o cuando la madre del menor sea diagnosticada con depresin posparto, infeccin gestacional, o que haya pasado por complicaciones durante la gestacin, e incluso que sea consumidora de sustancia psicoactivas.
    .row.justify-content-center.mb-5
      .col-lg-2.col-4.mb-lg-0.mb-3(data-aos="fade-right"): img(src='@/assets/curso/temas/tema1/56.svg', alt='')
      .col-lg-10(data-aos="fade-left")
        p La RPMS ha considerado realizar una categorizacin del riesgo, teniendo en cuenta condiciones biopsicosociales que afectan directamente a las nias y los nios; estos criterios no solo tienen en cuenta condiciones propias de la niez, sino que abarcan condiciones que afectan a su familia y, por lo tanto, pueden comprometer negativamente su desarrollo integral.
          br
          br
          |Se consideran #[b nias y nios con mayor riesgo], aquellos con presencia de uno o ms de los siguientes factores o antecedentes:
    .mn(style="background-color:#FFE0DD ").p-5.mb-5
      .tarjeta.rounded-3.color-acento-contenido.p-2(data-aos="fade-right")
        h4.mb-0.mx-2.text-center Condiciones perinatales y neonatales
      .p-4(style="background-color: #AFCDF5 ").mb-5(data-aos="zoom-in")
        .row.justify-content-center
          .col-lg-4.col-8.mb-lg-0.mb-3: img(src='@/assets/curso/temas/tema1/57.png', alt='')
          .col-lg-8
            p  #[b Bajo peso al nacer:] menor de 2500 gramos. La atencin se realizar segn los Lineamientos del Programa Madre Canguro que estn vigentes.
              br
              br
              | #[b Prematurez:] edad gestacional menor de 37 semanas, cuya atencin se realizar segn los Lineamientos del Programa Madre Canguro que estn vigentes.
              br
              br
              | #[b Hospitalizacin neonatal mayor a 7 das.]
              br
              br
              | #[b Patologa perinatal o neonatal significativa:] por ejemplo: asfixia perinatal, trastornos metablicos persistentes, convulsiones, trastornos respiratorios, sepsis neonatal temprana que no respondi a primera lnea de antibitico; necesidad de reanimacin neonatal, ventilacin mecnica, ciruga neonatal, terapia de reemplazo renal, puncin lumbar, o cualquier otro estudio o procedimiento invasivo.
              br
              br
              |  Hija(o) de madre con #[b infeccin gestacional o perinatal con alto riesgo] de malformaciones, problemas del desarrollo o necesidad de atencin especial en salud en el nio (Por ej: exposicin a Sfilis, Toxoplasmosis, Rubola, Citomegalovirus, Herpes virus, VIH, Zika virus). 
              br
              br
              | Hija(o) de madre con #[b complicaciones durante la gestacin] que hayan tenido repercusin en el hijo (en edad fetal, perinatal, neonatal o durante el primer ao de vida).
      .tarjeta.rounded-3.color-acento-contenido.p-2(data-aos="fade-right")
        h4.mb-0.mx-2.text-center Alteraciones de la salud o el desarrollo
      .p-4(style="background-color: #AFCDF5 ").mb-5(data-aos="zoom-in")
        .row.justify-content-center
          .col-lg-4.col-8.mb-lg-0.mb-3: img(src='@/assets/curso/temas/tema1/58.png', alt='')
          .col-lg-8
            p  #[b Anomala congnita mayor o mltiple.]
              br
              br
              | Nias o nios con #[b sospecha de problemas del desarrollo infantil] (Por ej: retraso, disociacin, desviacin o regresin del desarrollo).
              br
              br
              | Nia(o) con #[b tamizaje o estudio positivo para alteraciones congnitas] (hipotiroidismo congnito, errores innatos del metabolismo, hipoacusia, problemas visuales, etc.) o con sospecha de sndrome gentico (Sndrome de Down, Intersexualidad, etc.). 
              br
              br
              | Nias o nios que presenten un #[b evento de inters en salud pblica.]
      .tarjeta.rounded-3.color-acento-contenido.p-2(data-aos="fade-right")
        h4.mb-0.mx-2.text-center Condiciones sociales o familiares de riesgo
      .p-4(style="background-color: #AFCDF5 ").mb-5(data-aos="zoom-in")
        .row.justify-content-center
          .col-lg-4.col-8.mb-lg-0.mb-3: img(src='@/assets/curso/temas/tema1/59.png', alt='')
          .col-lg-8
            p  Hija(o) de madre con sospecha o diagnstico de #[b depresin postparto.]
              br
              br
              | Hija(o) de padres (especialmente la madre) con #[b consumo de sustancias psicoactivas], incluyendo aquellos con riesgo o sospecha de repercusin por consumo de sustancias psicoactivas (por ejemplo, sndrome de alcoholismo fetal, anomalas congnitas, retraso en el desarrollo, sndrome de abstinencia, etc.).
              br
              br
              | Hija(o) de #[b padres con enfermedad mental] con impacto en la estabilidad (estructural o funcional) de la familia o en el cuidado de los nios.
              br
              br
              | Nia(o) #[b acompaante de mujer en privacin de la libertad en centro carcelario.]
              br
              br
              | Nias o nios con #[b padres o hermanos con enfermedad catastrfica] (incluyendo las neoplasias y las condiciones degenerativas), con discapacidad o con otra condicin o situacin que afecte negativamente el cuidado del nio en primera infancia.
              br
              br
              | Nias o nios #[b vctimas de vulneracin o violacin de derechos humanos] con o sin proceso de restablecimiento de derechos en curso (por ejemplo, vctimas de violencia fsica, psicolgica, sexual, negligencia y abandono en el mbito familiar, escolar, institucional, comunitario, etc., intrafamiliar o testigos de violencia intrafamiliar, abandonados, explotados, secuestrados, traficados o expuestos al consumo de alcohol u otras sustancias psicoactivas).
              br
              br
              | Nias o nios pertenecientes a un hogar en #[b pobreza extrema o en situacin de calle.]
      .tarjeta.rounded-3.color-acento-contenido.p-2(data-aos="fade-right")
        h4.mb-0.mx-2.text-center Condiciones crnicas - discapacidad
      .p-4(style="background-color: #AFCDF5 ").mb-5(data-aos="zoom-in")
        .row.justify-content-center
          .col-lg-4.col-8.mb-lg-0.mb-3: img(src='@/assets/curso/temas/tema1/60.png', alt='')
          .col-lg-8
            p  #[b Condicin crnica] (neurolgica, cardiovascular, respiratoria, metablica, endocrinolgica, gastrointestinal, renal o de otro tipo).
              br
              br
              | #[b Discapacidad] (de cualquier tipo o con alto riesgo de presentarla por una condicin identificada, por ejemplo, anotia, anoftalma, agenesia de extremidades, resultados de estudio metablico para enfermedades neurodegenerativas, etc.).
              br
              br
              | Nia(o) con necesidad de #[b tratamiento con medicamentos de forma crnica.]
              br
              br
              | Nia(o) con #[b antecedente de hospitalizaciones recurrentes] (ms de 3 en el ltimo ao), hospitalizaciones prolongadas (mayores a 2 semanas) o que haya necesitado atencin en Unidad de Cuidados Intensivos Peditricos.
    p.mb-5(data-aos='fade-right') Teniendo en cuenta la definicin de lo que es y tiene por objetivo una valoracin integral en primera infancia, se debe recordar que, de encontrarse algn factor de riesgo, se procede de acuerdo a lo establecido en la Resolucin 3280 de 2018 y los protocolos institucionales a cargo de las intervenciones que sean definidas por profesionales entrenados para la atencin de la nia o nio. A continuacin, se expone el procedimiento indicado de acuerdo con la resolucin mencionada, para la recopilacin de informacin, que incluya indagacin de antecedentes y revisin por sistemas.
    .tarjeta.rounded-3.color-acento-contenido.p-2(data-aos="fade-right").mb-5
        h4.mb-0.mx-2.text-center Proceso de indagacin
    .tarjeta.bg-slyder.p-lg-5.p-3.py-1.mb-5
      SlyderA(tipo='b')
        .row.mx-3.justify-content-center
          .col-lg-7.mb-4.mb-md-0
            .row.justify-content-center
              .col-2.mb-lg-0.mb-3
                .numero
                  p.h2.m-4 1 
              .col-lg-10
                p Antecedentes personales, mdicos, hospitalarios, transfusionales, farmacolgicos, exposicin al humo de tabaco, alrgicos, quirrgicos, vacunacin, comportamiento general y relaciones interpersonales.
          .col-lg-5.col-8: img(src='@/assets/curso/temas/tema1/61.png', alt='')
        .row.mx-3.justify-content-center
          .col-lg-7.mb-4.mb-md-0
            .row.justify-content-center
              .col-2.mb-lg-0.mb-3
                .numero
                  p.h2.m-4 2
              .col-lg-10
                p Antecedentes familiares con nfasis en: enfermedades hereditarias, asma, tuberculosis, dermatitis atpica, problemas de desarrollo infantil, antecedente de muerte en hermanos, antecedentes de salud mental de los padres, consumo de alcohol u otras sustancias psicoactivas, exposicin a violencias de cualquier tipo, sucesos vitales (duelo, divorcio de los padres).
          .col-lg-5.col-8: img(src='@/assets/curso/temas/tema1/62.png', alt='')
        .row.mx-3.justify-content-center
          .col-lg-7.mb-4.mb-md-0
            .row.justify-content-center
              .col-2.mb-lg-0.mb-3
                .numero
                  p.h2.m-4 3
              .col-lg-10
                p Indagar sobre el progreso en los hitos del desarrollo de la nia(o), sobre la adaptacin e integracin en los entornos de educacin inicial, as como preocupaciones de los padres o cuidadores respecto al desarrollo de los nios.
          .col-lg-5.col-8: img(src='@/assets/curso/temas/tema1/63.png', alt='')
        .row.mx-3.justify-content-center
          .col-lg-7.mb-4.mb-md-0
            .row.justify-content-center
              .col-2.mb-lg-0.mb-3
                .numero
                  p.h2.m-4 4
              .col-lg-10
                p Prcticas de crianza y cuidado (comunicacin con la nia o el nio y expresin de afecto, actividades para favorecer el desarrollo y juego, exposicin a pantallas, establecimiento de lmites, vivencia de violencia en el hogar, prevencin de accidentes incluyendo seguridad vial).
          .col-lg-5.col-8: img(src='@/assets/curso/temas/tema1/64.png', alt='')
        .row.mx-3.justify-content-center
          .col-lg-7.mb-4.mb-md-0
            .row.justify-content-center
              .col-2.mb-lg-0.mb-3
                .numero
                  p.h2.m-4 5
              .col-lg-10
                p Alimentacin en la nia(o) menor de 6 meses haciendo nfasis en la lactancia materna (indagar lactancia materna no exclusiva - Indagando si la nia(o) ha recibido cualquier lquido, alimento o leche diferente a la materna el da o noche anterior). Alimentacin de la nia(o) mayor de 6 meses indagar sobre consumo y hbitos alimentarios indicando tipo de alimentos, frecuencia de consumo, cantidad, forma de preparacin y texturas, adicin de sal y azcar y tipo de alimentos, para lo cual puede ser til la indagacin de la alimentacin en las ltimas 24 horas. Verificar si despus del ao ya consume la dieta familiar.
          .col-lg-5.col-8: img(src='@/assets/curso/temas/tema1/65.png', alt='')
        .row.mx-3.justify-content-center
          .col-lg-7.mb-4.mb-md-0
            .row.justify-content-center
              .col-2.mb-lg-0.mb-3
                .numero
                  p.h2.m-4 6
              .col-lg-10
                p Verificar la realizacin y los resultados de las pruebas de tamizaje neonatal (auditivo por tecnologa dura, tamizaje de errores innatos del metabolismo por muestra de sangre, cardiopata congnita y visual por examen fsico).
          .col-lg-5.col-8: img(src='@/assets/curso/temas/tema1/66.png', alt='')
        .row.mx-3.justify-content-center
          .col-lg-7.mb-4.mb-md-0
            .row.justify-content-center
              .col-2.mb-lg-0.mb-3
                .numero
                  p.h2.m-4 7 
              .col-lg-10
                p Establecimiento de rutinas y hbitos saludables relacionados con: el sueo, bao, cambio de paal (cuntas veces al da, caractersticas de la deposicin), lavado de manos, cuidado bucal, foto proteccin, evolucin del control de esfnteres a partir de dos (2) aos y limpieza del rea perineal y juego o movimiento activo de acuerdo a la edad.
          .col-lg-5.col-8: img(src='@/assets/curso/temas/tema1/67.png', alt='')
        .row.mx-3.justify-content-center
          .col-lg-7.mb-4.mb-md-0
            .row.justify-content-center
              .col-2.mb-lg-0.mb-3
                .numero
                  p.h2.m-4 8 
              .col-lg-10
                p Valoracin o actualizacin de las condiciones de vida, del contexto social y las redes de apoyo social y comunitarias de la familia, pertenencia social y cultural que considere pertenencia tnica a travs de Ecomapa.
          .col-lg-5.col-8: img(src='@/assets/curso/temas/tema1/68.png', alt='')
        .row.mx-3.justify-content-center
          .col-lg-7.mb-4.mb-md-0
            .row.justify-content-center
              .col-2.mb-lg-0.mb-3
                .numero
                  p.h2.m-4 9
              .col-lg-10
                p Avance en los compromisos acordados en sesiones de educacin para la salud individual previas as como otras inquietudes de los padres.
          .col-lg-5.col-8: img(src='@/assets/curso/temas/tema1/69.png', alt='')
        .row.mx-3.justify-content-center
          .col-lg-7.mb-4.mb-md-0
            .row.justify-content-center
              .col-2.mb-lg-0.mb-3
                .numero
                  p.h2.m-4 10
              .col-lg-10
                p Valoracin o actualizacin de la conformacin y dinmica de la familia a travs del Familiograma, indagar sobre las capacidades y recursos personales y familiares que inciden en el cuidado de la salud familiar, evaluar el grado de satisfaccin de la funcionalidad familiar, a travs del #[b Apgar familiar.]
          .col-lg-5.col-8: img(src='@/assets/curso/temas/tema1/70.png', alt='')

