      |Inicialmente, se deben reconocer los signos vitales. Deben tomarse e interpretarse de acuerdo con la edad de la nia o el nio; estas constantes vitales son: frecuencia cardiaca, frecuencia respiratoria, temperatura, saturacin por pulso oximetra. Para la toma de tensin arterial debe tenerse en cuenta que se realiza a nias o nios mayores de 3 aos o a menores de esta edad. Si tienen sntomas recurrentes de cualquier tipo o antecedente de cateterizacin umbilical en su edad neonatal, se debe tener presente la tabla de valores de constantes vitales segn corresponda la edad de cada nia o nio; a continuacin, se muestra visualmente cada uno de estos valores.
    .tarjeta.rounded-3.color-acento-contenido.p-2(data-aos="fade-right").mb-5
        h4.mb-0.mx-2.text-center Valores de los signos vitales
