    p.mb-5(data-aos='fade-right') Adicionalmente, se deben tener en cuenta otros aspectos particulares a realizar en cada consulta:
    .row.justify-content-center.mb-4
      .col-lg-8.mb-lg-0.mb-3(data-aos="fade-right")
        AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--gris")
          .row(titulo="Valoracin del desarrollo")
            .col-lg-2.col-4.mb-lg-0.mb-3
              figure
                img(src='@/assets/curso/temas/tema1/73.svg', alt='')      
            .col-lg-10
              p Pretende realizar el seguimiento presentado en el desarrollo de las nias o los nios en primera infancia con apoyo de padres y/o cuidadores, maestros quienes se encuentran en el entorno y cumplen un papel vital sobre el desarrollo de la nia o el nio, y que a travs de la aplicacin directa de la #[b Escala Abreviada de Desarrollo-3], ante la sospecha de alteraciones del desarrollo, se pudiera aplicar tambin el test #[b M-Chat R/F], instrumento til para tamizaje del trastorno del espectro autista, especficamente, a todas las nias y los nios entre los 18 y los 30 meses de edad.
          .row(titulo="Valoracin del estado nutricional y de parmetros antropomtricos")
            .col-lg-2.col-4.mb-lg-0.mb-3
              figure
                img(src='@/assets/curso/temas/tema1/74.svg', alt='')      
            .col-lg-10
              p Se realiza tomando las medidas de peso, talla y permetro ceflico, para el clculo de los ndices relacionados con los mismos. Tambin, se debe tener en cuenta algunos aspectos culturales, geogrficos, econmicos y otros como lo son: acceso y disponibilidad a esos alimentos y lo ms importante, cubrir aquellos nutrientes crticos que ya no son cubiertos por la lactancia materna como: hierro, zinc y vitamina D, y que sern fundamentales para el adecuado desarrollo de las nias y los nios.
          .row(titulo="Valoracin de la salud sexual")
            .col-lg-2.col-4.mb-lg-0.mb-3
              figure
                img(src='@/assets/curso/temas/tema1/75.svg', alt='')      
            .col-lg-10
              p Se debe prestar especial atencin a la deteccin de signos de violencia fsica y sexual, as como anomalas congnitas.
          .row(titulo="Valoracin de la salud visual")
            .col-lg-2.col-4.mb-lg-0.mb-3
              figure
                img(src='@/assets/curso/temas/tema1/76.svg', alt='')      
            .col-lg-10
              p Se aplica de acuerdo al tamizaje encontrado en la Resolucin 3280 de 2018, en el cual se realiza un examen oftalmoscpico, el cual explora y evala las estructuras. Se debe valorar la agudeza visual a partir de los tres (3) aos de edad usando optotipos morfoscpicos o angulares. Tener en cuenta para la valoracin las preocupaciones de los padres.
          .row(titulo="Valoracin de la salud auditiva y comunicativa")
            .col-lg-2.col-4.mb-lg-0.mb-3
              figure
                img(src='@/assets/curso/temas/tema1/77.svg', alt='')      
            .col-lg-10
              p Se realiza una inspeccin del odo, que debe encontrarse ntegro a travs de la valoracin visual, la otoscopia, y de las funciones de la articulacin, voz, habla y desempeo para comunicarse. Se recomienda aplicar #[b la lista de chequeo de factores de riesgo de las enfermedades del odo, alteraciones auditivas, vestibulares y de la comunicacin.] Durante la consulta se debe aplicar el cuestionario #[b VALE.] Verificar resultado de tamizaje auditivo, especialmente en nios y nias con riesgo de hipoacusia.
          .row(titulo="Valoracin de la salud bucal")
            .col-lg-2.col-4.mb-lg-0.mb-3
              figure
                img(src='@/assets/curso/temas/tema1/78.svg', alt='')      
            .col-lg-10
              p Su objetivo est no solo en el reconocimiento del estado de las estructuras dentomaxilofaciales y de su funcin, sino que adems valora los procesos de masticacin, deglucin, procesos comunicativos y del lenguaje asociados al rango de edad al que se est consultando.
          div(titulo="Valoracin de la salud mental")
              p En este marco es importante indagar acerca de la calidad de la relacin de los padres hacia los hijos, dado que este factor es importante como protector o riesgo de posibles problemas y /o trastornos mentales. Identificar los hallazgos a partir de la anamnesis.
                br
                br
                |Se debe valorar la presencia de riesgos psicosociales para violencias y de exposicin a violencias, exposicin a lesiones en los nios o nias por consumo de sustancias psicoactivas del padre o madre, o algn factor de riesgo como trastorno mental, convulsiones o ausencias, situaciones o momentos significativos (duelo o prdidas), alteraciones o anormalidades relacionadas con la salud mental o neurolgica.
                br
                br
                |Las alteraciones de la salud mental en la primera infancia tienen su expresin principal en los procesos del desarrollo; aunque pueden presentarse alteraciones en el apego, as como experiencias adversas en la niez, manifestaciones iniciales de trastornos del espectro autista, ansiedad, depresin, entre otras. Debe considerarse la valoracin del riesgo psicosocial del cuidador como factor clave e indicador de riesgo de la salud mental en las nias y nios en primera infancia.
      .col-lg-4.d-lg-block.d-none(data-aos="fade-left"): img(src='@/assets/curso/temas/tema1/79.png', alt='')
     

    p.mb-5 Durante la consulta se debe aplicar el cuestionario VALE. El siguiente video expone la aplicacin de este instrumento.

    figure.mb-5
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/hqgNAfBtYII" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
      figcaption Video. Interpretacin de resultados Escala Abreviada de Desarrollo 3 (EAD-3).
                
    .p-3(style="background-color: #E3EEFC").mb-4
      .row.justify-content-center
        .col-lg-1.mb-lg-0.mb-3.col-4: img(src='@/assets/curso/temas/tema1/81.svg', alt='')
        .col-lg-11
          p Ante cualquier alteracin encontrada en la anamnesis, en el examen fsico o en la aplicacin de algunos de los instrumentos usados durante la valoracin, debe canalizarse a la ruta integral de atencin en salud para poblacin con riesgo o a la atencin resolutiva por profesional especializado, segn sea el caso, para el manejo respectivo.
    .row.justify-content-center.mb-5
      .col-lg-4.col-8.mb-lg-0.mb-3(data-aos="fade-right"): img(src='@/assets/curso/temas/tema1/82.png', alt='')
      .col-lg-8
        p Ahora bien, en cuanto a la informacin de salud, se hace necesario recordar la importancia de brindar informacin de acuerdo con los hallazgos, las necesidades identificadas en la consulta y las inquietudes o necesidades expresadas por la nia o el nio, as como de los padres o cuidadores.
          br
          br
          |Es importante abordar pautas de prevencin de violencias y brindar elementos para detectar estas situaciones y activar la ruta de atencin correspondiente. De acuerdo al momento de desarrollo en el que se encuentre, se deben dar orientaciones para promoverlo, pueden ensearse hitos del desarrollo de acuerdo con la edad especfica en la que se encuentran, tal como se representa en la grfica, que pueda favorecer los procesos de vigilancia, involucrando a los padres y cuidadores en el proceso.
    .tarjeta.bg-slyder.p-lg-5.p-4.mb-5
      SlyderA(tipo='b')
        .row
          .col-lg-7.mb-4.mb-md-0
            h5 Prcticas de crianza protectoras y basadas en derechos- prevencin de las violencias.
            p Es necesario comentar con los padres y cuidadores respecto a prcticas de crianza protectoras basadas en derechos, partiendo del reconocimiento de los nios y las nias desde su particularidad, a travs de una comunicacin permanente, que avance en el establecimiento de hbitos y rutinas.
          .col-lg-5
            figure
              img(src='@/assets/curso/temas/tema1/83.png', alt='')
        .row.justify-content-center
          .col-lg-7.mb-3.mb-md-0
            h5 Promocin de la salud, del bienestar, del crecimiento y del desarrollo.
            p Indique a los padres o cuidadores que pasen tiempo con el nio en actividades agradables, que jueguen y se comuniquen con el nio, que lo escuchen y le muestren comprensin y respeto. Que lo protejan de cualquier forma de violencias, incluidas la intimidacin/acoso y la exposicin a la violencia en el hogar, en la escuela y en la comunidad. Que prevean cambios importantes en su vida (como el inicio de la educacin escolar o el nacimiento de un hermano), reconociendo que son situaciones estresantes que requieren de su atencin y apoyo. Informe sobre la necesidad de garantizarle las condiciones para que duerma cmodo y suficientes horas.
              br
              br
              |Tener rutinas regulares de ir a la cama a determinada hora, retirar el televisor u otros dispositivos electrnicos con pantallas del rea donde duerme.
          .col-lg-5.col-8
            figure
              img(src='@/assets/curso/temas/tema1/84.png', alt='')
        .row
          .col-lg-7.mb-4.mb-md-0
            h5 Alimentacin saludable y promocin de la lactancia materna.
            p Informe sobre la alimentacin saludable y la necesidad de mantenerse activos fsicamente mediante actividades diarias, juegos o deportes, durante un mnimo de 30 minutos. Brindar informacin referente a la lactancia materna y sus beneficios en los ms pequeos, as como acompaar y guiar el paso a la alimentacin complementaria al cumplir los 6 meses, junto con una adecuada manipulacin de alimentos, son componentes fundamentales para su salud y bienestar.
          .col-lg-5
            figure
              img(src='@/assets/curso/temas/tema1/85.png', alt='')
        .row
          .col-lg-7.mb-4.mb-md-0
            h5 Promocin de hbitos y estilos de vida saludable.
            p Recomiende a los padres motivar a sus hijos a participar todo lo posible en actividades en la escuela, la comunidad y otras actividades sociales, que pasen tiempo con familiares y amigos de confianza. Recomiende a los padres y cuidadores evitar el uso de drogas, alcohol y tabaco. Elogie al cuidador y al nio por sus esfuerzos en el cuidado de su salud.
          .col-lg-5
            figure
              img(src='@/assets/curso/temas/tema1/86.png', alt='')
        .row.justify-content-center
          .col-lg-7.mb-4.mb-md-0
            h5 Prevencin de enfermedades y accidentes.
            p Los cuidadores y las familias en general deben tener presente favorecer entornos protectores, favorecedores de su desarrollo: evitando la exposicin al humo de tabaco, estableciendo momentos para el juego, la actividad fsica y el deporte, regulando tiempos de exposicin a pantallas, evitando el ruido para una audicin segura, entre otras ms. Procurar pautas de cuidado de su salud que puedan ir incorporando como cuidado propio: desde la salud bucal, pasando por el cuidado de la audicin y la visin, aclarando signos de alarma y orientaciones para acudir al servicio de urgencias en los casos que corresponda.
              br
              br
              |Incluya informacin sobre derechos de los nios, derecho a la salud, y mecanismos de exigibilidad de los mismos y en el marco de la consulta, ponga de presente al nio o la nia como centro de esta atencin.
          .col-lg-5.col-8
            figure
              img(src='@/assets/curso/temas/tema1/87.png', alt='')
        .row
          .col-lg-7.mb-4.mb-md-0
            h5 Signos de alarma y manejo bsico de para condiciones prevalentes.
            p Finalmente, brinde informacin sobre los derechos de las madres, la importancia del empoderamiento de las mujeres para trabajar actividades de cuidado para ellas y sus hijos, as como para promover el fortalecimiento del rol del padre como cuidador de sus hijas e hijos.
          .col-lg-5
            figure
              img(src='@/assets/curso/temas/tema1/88.png', alt='')
    p.mb-5(data-aos='fade-right') Sumado a esto, tambin se deben tener en cuenta los instrumentos, insumos y dispositivos, para dar cumplimiento a los estndares de habilitacin para la consulta de pediatra, medicina general, medicina familiar o enfermera, como lo son:
