    .p-3(style="background-color: #E3EEFC").mb-4
      .row.justify-content-center
        .col-lg-1.mb-lg-0.mb-3.col-4: img(src='@/assets/curso/temas/tema1/90.svg', alt='')
        .col-lg-11
          p Las polticas pblicas de una sociedad deben orientar sus objetivos a la promocin de un cuidado carioso y sensible que vele por la salud y nutricin de las nias y los nios, y que sea capaz de responder a sus necesidades, su seguridad y proteccin, y que promuevan el aprendizaje desde muy temprana edad, favoreciendo su desarrollo integral, orientado a una promocin no solo individual, sino colectiva, que apunte al mejoramiento de sus condiciones de vida a largo plazo.
    .row.justify-content-center.mb-5
      .col-lg-8.mb-lg-0.mb-3(data-aos="fade-right")
        p Finalmente, de acuerdo con la publicacin de la Comisin de Lancet, en el ao 2020, Un futuro para los nios del mundo, dentro del desarrollo infantil, es importante analizar los determinantes sociales como condiciones que en muchos casos pueden establecer un estado de desigualdad, adems de propiciar ambientes y sistemas donde el desarrollo se ve amenazado por circunstancias adversas que no propicien el adecuado desarrollo del individuo, tales como inequidad en las posiciones sociales, acceso a la educacin, bajos ingresos, sexo femenino, difcil acceso a los sistemas de salud y la desigualdad de la distribucin de los recursos.
          br
          br
          |A pesar de los esfuerzos que han realizado los pases en mejorar la supervivencia, la nutricin y la educacin, en las ltimas dcadas, el futuro de las nias y los nios se ve directamente afectado por condiciones como el cambio climtico, la degradacin ecolgica, la migracin, las situaciones de conflicto armado, as como persistencia de las inequidades sociales.
      .col-lg-4.col-8.d-none.d-lg-block(data-aos="fade-left"): img(src='@/assets/curso/temas/tema1/91.png', alt='')
    .p-3(style="background-color: #E3EEFC").mb-4
      .row.justify-content-center
        .col-lg-1.mb-lg-0.mb-3.col-4: img(src='@/assets/curso/temas/tema1/92.svg', alt='')
        .col-lg-11
          p La evidencia permite afirmar que la pobreza infantil medida en trminos de ingreso familiar, educacin y ocupacin de los padres y de privaciones materiales, se asocia con un conjunto diverso de cambios estructurales y funcionales en el sistema nervioso. Los sistemas neuronales ms involucrados en tales cambios pareceran ser aquellos correspondientes a procesos autorregulatorios cognitivos y emocionales, de lenguaje y de aprendizaje. (Clark, H.& Awa, M., et. al, 2020)
    .row.justify-content-center.mb-5
      .col-lg-4.col-8(data-aos="fade-right"): img(src='@/assets/curso/temas/tema1/93.png', alt='')
      .col-lg-8(data-aos="fade-left")
        p Los estudios recogidos por la publicacin de Clark, H.& Awa, M. et. al, (2020), han demostrado, de acuerdo con el enfoque de curso de vida, teniendo en cuenta sus principios, la presencia de factores protectores como son la proteccin de sus derechos para vivir, crecer y participar, exponindose a ambientes seguros, evitando la exposicin a la discriminacin, porque acta como un potenciador de la salud y el desarrollo, mientras que factores de vulnerabilidad como la malnutricin, las violencias de tipo fsico, psicolgico y sexual afectan directamente su desarrollo integral, teniendo en cuenta que entre ellos se encuentran la brecha correspondiente a los determinantes sociales; por lo tanto, el bienestar infantil est anclado en los derechos y equidad a lo largo de su curso de vida, con el objetivo de mejorar factores protectores y atenuantes de la vulnerabilidad, como compromiso en que deberan avanzar los pases. La siguiente figura muestra lo anteriormente mencionado.
